:root {
  --softRed: hsl(10, 79%, 65%);
  --cyan: hsl(186, 34%, 60%);
  --darkBrown: hsl(25, 47%, 15%);
  --mediumBrown: hsl(28, 10%, 53%);
  --cream: hsl(27, 66%, 92%);
  --veryPaleOrange: hsl(33, 100%, 98%);
}

* {
  margin: 0;
  padding: 0;
}

.App {
  font-family: "DM Sans", sans-serif;
  background-color: var(--cream);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.main {
  width: 93%;
  max-width: 500px;
}

.header {
  display: flex;
  justify-content: space-between;
  background-color: var(--softRed);
  padding: 20px;
  border-radius: 10px;
  color: white;
  margin-bottom: 20px;
}

.details {
  background-color: var(--veryPaleOrange);
  padding: 20px;
  border-radius: 10px;
}

.chart {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  border-bottom: 4px solid var(--cream);
  padding: 60px 0 30px 0;
}

.chart div {
  border-radius: 5px;
  cursor: pointer;
}

.bar {
  text-align: center;
  position: relative;
}

.bar:hover .spent {
  display: block;
}

.barEl:hover {
  opacity: 0.8;
}

.bar p {
  text-align: center;
  color: var(--mediumBrown);
  margin-top: 10px;
}

.spent {
  display: none;
  position: absolute;
  font-size: 12px;
  background-color: var(--darkBrown);
  border-radius: 5px;
  padding: 5px;
  color: white !important;
  top: -45px;
  width: 100%;
  left: -5px;
}

.total {
  display: flex;
  justify-content: space-between;
  padding-top: 30px;
}

.thisMonth {
  align-self: center;
}

.lastMonth {
  align-self: flex-end;
}

.total h2 {
  font-size: 30px;
}

.total p {
  color: var(--mediumBrown);
}

.total h4 {
  text-align: right;
}

.attribution {
  margin-top: 20px;
  font-size: 11px;
  text-align: center;
}
.attribution a {
  color: hsl(228, 45%, 44%);
}
